.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 50em;
  width: 90em;
  align-items: center;
}

.Laatikko {
  padding-left: 1em;
  border: 1px solid gray;
  width: 30%;
  margin-top: 2em;
  margin-bottom: 1em;
}

.Haku {
  font-weight: 100;
  margin-top: 15em;
  margin-bottom: 2em;
}

.leaflet-container {
  margin-top: 5em;
  margin-left: 5em;
  margin-bottom: 5em;
  width: 40em;
  height: 80em;
  }

input {
  width: 200px;
  height: 30px;
}

@media screen and (max-width: 1280px) {
  .App {
    width: 70em;
  }
}

@media screen and (max-width: 999px) {
  .App {
    flex-wrap: wrap;
    height: 50em;
    width: 50em;
  }

  .leaflet-container {
    margin-left: 3em;
    width: 40em;
    height: 60em;
  }

  .Hakukentta {
    margin-top: 10em;
  }
}

@media screen and (max-width: 776px) {
  .App {
    flex-wrap: column;
    align-items: center;
    margin-left: 0.1em;
    height: 80em;
    width: 40em;
  }

  .leaflet-container {
    width: 40em;
    height: 50em;
    margin-bottom: 1em;
  }

  .Laatikko {
    margin-top: 1em;
  }

  .Haku {
    margin-bottom: 1em;
    margin-top: 1em;
  }
}

.footer{
  max-height: 70px;
  height: 5em;
  margin-top: 1em;
  padding: 1em;
  background-color: rgb(181, 201, 216);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}